// Custom Hook fr die Validierung des Kontaktformulars

import { useState, useRef } from "react";

function useValidateContactForm() {
  const nameInput = useRef("");                     // Ref fr das Namenfeld    
  const emailInput = useRef("");                    // Ref fr das Email-feld    
  const messageInput = useRef("");                  // Ref fr das Message-Feld    
  const newsletterInput = useRef("");               // Ref fr die Newsletter-Checkbox    
  const termsAndConditionsInput = useRef("");       // Ref fr die Terms and Conditions Checkbox

  const [responseMessage, setResponseMessage] = useState("");                       // Success- oder Fail-Meldung fr das gesamte Formular    
  const [nameResponseMessage, setNameResponseMessage] = useState("");               // Fehlermeldungsvariable fr das Namenfeld
  const [emailResponseMessage, setEmailResponseMessage] = useState("");             // Fehlermeldungsvariable fr das Email-Feld
  const [messageResponseMessage, setMessageResponseMessage] = useState("");         // Fehlermeldungsvaroabe fr das Message-Feld
  const [termsAndConditionsMessage, setTermsAndConditionsMessage] = useState("");   // Fehlermeldungsvariable fr die Terms and Conditions Checkbox

  // Validierungsmethode
  const validateForm = () => {
    
    // berprfung ob Namenfeld leer ist
    if (nameInput.current.value === "") {
      setNameResponseMessage("This field is required");
      setTimeout(() => {
        setNameResponseMessage("");
      }, 3000);
      return false;
    } 
    // berprfung ob Namenfeld potentiell gefhrliche Zeichen enthlt
        else if (
      nameInput.current.value.match(/([<>/|{}[\*\]\\!"$%()=?`+\-#])/)
    ) {
      setNameResponseMessage("Unallowed characters. Try again");
      setTimeout(() => {
        setNameResponseMessage("");
      }, 3000);
      return false;
    } 
    // berprfung ob Email-Feld leer ist
    else if (emailInput.current.value === "") {
      setEmailResponseMessage("This field is required");
      setTimeout(() => {
        setEmailResponseMessage("");
      }, 3000);
      return false;
    } 
    // berprfung ob Email-Feld potentiell gefhrliche Zeichen enthlt
    else if (
      emailInput.current.value.match(/([<>/|{}[\*\]\\!"$%()=?`+\-#])/)
    ) {
      setEmailResponseMessage("Unallowed characters. Try again");
      setTimeout(() => {
        setEmailResponseMessage("");
      }, 3000);
      return false;
    } 
    // berprfung ob Message-Feld leer ist
    else if (messageInput.current.value === "") {
      setMessageResponseMessage("This field is required");
      setTimeout(() => {
        setMessageResponseMessage("");
      }, 3000);
      return false;
    } 
    // berprfung ob Message-Feld potentiell gefhrliche Zeichen enthlt
    else if (
      messageInput.current.value.match(/([<>/|{}[\*\]\\!"$%()=?`+\-#])/)
    ) {
      setMessageResponseMessage("Unallowed characters. Try again");
      setTimeout(() => {
        setMessageResponseMessage("");
      }, 3000);
      return false;
    } 
    // berprfung, ob die Terms and Conditions angenommen worden sind
    else if (!termsAndConditionsInput.current.checked) {
      setTermsAndConditionsMessage("Please accept our terms and conditions");
      setTimeout(() => {
        setTermsAndConditionsMessage("");
      }, 3000);
    } 
    // Hier wird die Success-Meldung ausgegeben, falls alle berprfungen bestanden wurden
    else {
      setResponseMessage("Your message has been sent!");
      setTimeout(() => {
        setResponseMessage("");
      }, 5000);
      return true;
    }
  };

  // Ausgabe aller Refs, Fehlermeldung-Strings und der Validierungsmethode
  return {
    nameInput,
    emailInput,
    messageInput,
    newsletterInput,
    termsAndConditionsInput,
    responseMessage,
    nameResponseMessage,
    emailResponseMessage,
    messageResponseMessage,
    termsAndConditionsMessage,
    validateForm,
  };
}

export default useValidateContactForm;
